import React from 'react';

import Grid from "@mui/material/Grid";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";

import {createTheme, ThemeProvider} from "@mui/material/styles";

import SkillsCard from "./SkillsCard";

import Button from "@mui/material/Button";
import Link from "@mui/material/Link";

import Paper from "@mui/material/Paper";
import PersonPinOutlined from "@mui/icons-material/PersonPinOutlined"
import KeyboardArrowDownRounded from "@mui/icons-material/KeyboardArrowDownRounded";
import ArrowForwardRoundedIcon from '@mui/icons-material/ArrowForwardRounded';
import { Divider } from '@mui/material';

import LoadBox from "./LoadBox";

const theme = createTheme({
    breakpoints: {
      values: {
            xs: 0,
            sm: 872,
            md: 1300,
            lg: 1880,
            xl: 2700,
            xxl: 3600,
        },
    },
});

class Home extends React.Component {

    render () {
        let themeHighlight = "rgb(255, 230, 109)";

        let startPageStyle = {
            height: "100vh",
            width: "100%",
            borderRadius: "0",
            backgroundColor: "rgba(36, 32, 32, 0.95)",
            overflow: "hidden",
            position: "relative",
            display: "flex",
            justifyContent: "center",
        }
        let subtitleStyle = {
            color: "white",
            fontFamily: 'Source Sans',
            fontWeight: "600",
            fontSize: "2.5vmin",
        }
        let titleStyle = {
            color: themeHighlight,
            fontFamily: 'Bebas Neue',
            fontSize: "10vmin",
            lineHeight: "11vmin",
        }
        let blockStyle = {
            width: "50vw",
            paddingTop: "20vh",
        }
        let dividerStyle = {
            backgroundColor: themeHighlight,
            opacity: "75%",
        }
        let iconStyle = {
            color: themeHighlight,
            fontSize: "10vmin",
        }
        let arrowStyle = {
            color: themeHighlight,
            position: "absolute",
            bottom: "2vh",
            margin: "auto",
            opacity: "50%",
            transition: "all 200ms",
            justify: "center",
            display: "flex",
            cursor: "pointer",
        }
        let contentPageStyle = {
            backgroundColor: "rgba(0, 0, 0, 0)",
            padding: "100px",
            justifyContent: "center",
            display: "flex",
        }
        let cardStyle = {
            width: "80vw",
            margin: "auto",
            borderRadius: "6px",
            backgroundColor: "rgb(16, 16, 21)",
            border: "2px solid "+themeHighlight,
            textAlign: "center",
            padding: "20px",
        }
        let title2 = {
            color: themeHighlight,
            fontFamily: 'Source Sans',
            fontSize: "26px",
            fontWeight: "600",
        }
        let nextButton = {
            color: "black",
            backgroundColor: themeHighlight,
            fontFamily: "Source Sans",
            fontSize: "20px",
            borderRadius: "15px",
            padding: "10px",
            margin: "auto",
            fontWeight: "600",
        }
        let arrowStyle2 = {
            color: "black",
            fontSize: "30px"
        }
        let miniDist = 12;
        let smallDist = 6;
        let midDist = 4;
        let largeDist = 3;
        let hugeDist = 2;
        let massiveDist= 1;
        return (
            <div id="wrapperDiv">
                <LoadBox consoleText="whoami"/>
                <Paper style={startPageStyle} elevation={5}>
                    <div style={blockStyle}>
                        <Stack spacing={3}>
                            <Stack spacing={1} direction="row">
                                <PersonPinOutlined style={iconStyle}/>
                                <Typography style={titleStyle}>Hi, I'm Ryan.</Typography>
                            </Stack>
                            
                            <Divider style={dividerStyle}/>
                            <Typography style={subtitleStyle}>I'm a full-stack software developer based in Toronto, Ontario.</Typography>
                            <Typography style={subtitleStyle}>I build performant, concise software in the backend and elegant, 
                            intuitive applications in the frontend. I am currently taking my second year in Honours Specialisation Computer Science 
                            at the University of Western Ontario.</Typography>
                            <Typography style={subtitleStyle}>Alongside this, I also have a hobby in 3D art. You can see my showcased work in the 
                            projects page.</Typography>
                        </Stack>
                        
                    </div>
                    <div style={arrowStyle} id="arrowContainer" onClick={() => { window.scrollTo({top:document.documentElement.clientHeight, behavior: "smooth"}) }}>
                        <Stack style={{textAlign: "center"}}>
                            <Typography style={{...subtitleStyle, color: themeHighlight, lineHeight: "0px", fontWeight: "200", fontSize: "2vmin"}} id="arrowText">More downstairs</Typography>
                            <KeyboardArrowDownRounded style={{fontSize: "5vmin", justify: "center", margin: "auto", transform: "scale(1, 1)"}} id="arrowIcon"/>
                        </Stack>
                    </div>
                    
                </Paper>
                <Paper style={contentPageStyle} elevation={5}>
                    
                    <Paper style={cardStyle}>
                        <Typography style={title2}>Here are some of my skills and languages</Typography>
                        <ThemeProvider theme={theme}>
                            <Grid container style={{justifyContent: "center"}}>
                            <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                                    <SkillsCard title="Java" content="
                                    Java's universal JRE and global adoption make this one of my top choices for programming languages.
                                    " />
                                </Grid>
                                <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                                    <SkillsCard title="Ubuntu" content="
                                    I'm very familiar with CLI's, and use it on a daily basis to manage my self-hosted server and personal computer.
                                    " />
                                </Grid>
                                <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                                    <SkillsCard title="Blender" content="
                                    This has been my hobby for over 6 years. I'm extending this beyond art, intersecting it with machine learning.
                                    " />
                                </Grid>
                                <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                                    <SkillsCard title="C#" content="
                                    I use this to write less performance-intensive programs, and to work with APIs with the .NET RestSharp library.
                                    " />
                                </Grid>
                                <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                                    <SkillsCard title="Machine Learning" content="
                                    I've done multiple projects involving ML, with another planned for the future as well.
                                    " />
                                </Grid>
                                <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                                    <SkillsCard title="C" content="
                                    When I need all the performance out of my computer or strict control over data flow, C is my primary choice.
                                    " />
                                </Grid>
                                <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                                    <SkillsCard title="React" content="
                                    An easy-to-use library I use to swiftly build my sites with. I also code in the underlying JS, HTML, and CSS.
                                    " />
                                </Grid>
                                <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                                    <SkillsCard title="Assembly" content="
                                    Primarily for the understanding of code flow and logic, rather than a direct application of the language.
                                    " />
                                </Grid>
                                <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                                    <SkillsCard title="R & Python" content="
                                    My go-to languages for anything data-science related. It's great for visualising datasets and finding trends.
                                    " />
                                </Grid>
                            </Grid>
                        </ThemeProvider>
                        
                    </Paper>
                </Paper>
                {/*<Paper style={{...contentPageStyle, backgroundColor: "rgb(16, 16, 21)"}}>
                    <Link href="/projects" style={{textDecoration: "none"}}>
                        <Button style={nextButton}>Browse my projects <ArrowForwardRoundedIcon style={arrowStyle2}/></Button>
                    </Link>
                </Paper>*/}
            </div>
        );
    };
    componentDidMount () {
        window.addEventListener('scroll', this.handleScroll);
    }
    componentWillUnmount () {
        window.removeEventListener('scroll', this.handleScroll);
    }
    handleScroll (e) {
        let arrow = document.getElementById("arrowContainer");
        let arrowIcon = document.getElementById("arrowIcon");
        let arrowText = document.getElementById("arrowText");
        if (document.documentElement.scrollTop <= 0) {
            arrow.style.opacity = "50%";
            arrow.style.bottom = "2vh";
            arrowIcon.style.transform = "scale(1, 1)";
            arrowIcon.style.fontSize = "5vmin";
            arrowText.style.fontSize = "2vmin";
        } else if (document.documentElement.scrollTop < document.documentElement.clientHeight/2) {
            arrow.style.opacity = "25%";
            arrow.style.bottom = "5vh";
            arrowIcon.style.transform = "scale(1, 0.75)";
            arrowIcon.style.fontSize = "5vmin";
            arrowText.style.fontSize = "2vmin";
        } else {
            arrow.style.opacity = "5%";
            arrow.style.bottom = "10vh";
            arrowIcon.style.transform = "scale(1, 0.25)";
            arrowIcon.style.fontSize = "0px";
            arrowText.style.fontSize = "0px";
        }
    }


}
export default Home;