import React, { useEffect } from 'react';

import Typography from "@mui/material/Typography";
import Link from "@mui/material/Link";

import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";

import Divider from "@mui/material/Divider";

import CodeRounded from "@mui/icons-material/CodeRounded";

class TextCard extends React.Component {

    render () {
        var themeHighlight = "rgb(255, 230, 109)";
        var cardBackStyle = {
            margin: "20px",
            backgroundColor: "rgba(255, 255, 255, 0.2)",
            alignItems: "center",
            textAlign: "center",
            padding: "20px",
            paddingBottom: "0",
            borderRadius: "6px",
            transitionDuration: "200ms",
        }
        var titleStyle = {
            color: themeHighlight,
            fontSize: "28px",
            fontFamily: 'Source Sans',
            fontWeight: "600",
            overflow: "hidden",
        }
        var titleWrapperStyle = {
            height: "38px",
            overflow: "hidden",
            paddingBottom: "5px",
        }
        var captionStyle = {
            color: "white",
            fontSize: "18px",
            fontFamily: 'Source Sans',
        }
        var iconStyle = {
            color: themeHighlight,
            margin: "auto",
            fontSize: "50px",
            height: "50px",
        }
        var contentWrapperStyle = {
            overflow: "hidden",
            height: "105px",
            paddingTop: "5px",
        }
        return (
            
            <Link href={this.props.link} color="inherit" underline="none">
                <Paper style={cardBackStyle} elevation={0} onMouseEnter={this.liftCard} onMouseLeave={this.lowerCard}>
                    <Stack spacing={1}>
                        <CodeRounded style={iconStyle}/>
                        <div style={titleWrapperStyle}>
                            <Typography style={titleStyle}>{this.props.title}</Typography>
                        </div>
                        
                        <div style={contentWrapperStyle}>
                            <Typography style={captionStyle}>{this.props.content}</Typography>
                        </div>
                    </Stack>
                </Paper>
            </Link>
            
        );

    }
    /*

    liftCard (e) {
        let element = e.target;
        element.style.marginBottom = "30px";
    }
    lowerCard (e) {
        let element = e.target;
        element.style.marginBottom = "20px";
    }
    */
}
export default TextCard;