import React, { useEffect } from 'react';

import Grid from "@mui/material/Grid";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";


import Paper from "@mui/material/Paper";
import PhoneInTalkOutlined from "@mui/icons-material/PhoneInTalkOutlined"
import { Divider } from '@mui/material';

import LoadBox from "./LoadBox";

class Home extends React.Component {

    render () {
        var themeHighlight = "rgb(255, 230, 109)";

        var startPageStyle = {
            height: "100vh",
            width: "100%",
            borderRadius: "0",
            backgroundColor: "rgb(36, 32, 32)",
            overflow: "hidden",
        }
        var subtitleStyle = {
            color: "white",
            fontFamily: 'Source Sans',
            fontWeight: "600",
            fontSize: "2.5vmin",
        }
        var titleStyle = {
            color: themeHighlight,
            fontFamily: 'Bebas Neue',
            fontSize: "10vmin",
            lineHeight: "11vmin",
        }
        var blockStyle = {
            width: "50vw",
            marginLeft: "20vw",
            paddingTop: "20vh",
        }
        var dividerStyle = {
            backgroundColor: themeHighlight,
            opacity: "75%",
            width: "75%",
        }
        var iconStyle = {
            color: themeHighlight,
            fontSize: "10vmin",
        }
        return (
            <div>
                <LoadBox consoleText="ping 192.168.0.1"/>
                <Paper style={startPageStyle} elevation={5}>
                    <div style={blockStyle}>
                        <Stack spacing={3}>
                            <Stack spacing={1} direction="row">
                                <PhoneInTalkOutlined style={iconStyle}/>
                                <Typography style={titleStyle}>They call me Ryan,</Typography>
                            </Stack>
                            <Typography style={{...titleStyle, color: "white", opacity: "75%", fontSize: "6.5vmin"}}>But you can call me anytime.</Typography>
                            
                            <Divider style={dividerStyle}/>
                            <Typography style={subtitleStyle}>I don't have the contact forms set up yet, but if you're a recruiter or potential 
                            employer, feel free to reach out through the email or phone provided in my resume.</Typography>
                        </Stack>
                    </div>
                    
                </Paper>
            </div>
        );
    };
}
export default Home;