import React, { useEffect } from 'react';

import Typography from "@mui/material/Typography";
import Link from "@mui/material/Link";

import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";

import Divider from "@mui/material/Divider";

import PaletteOutlined from "@mui/icons-material/PaletteOutlined";
import test from "./img/test.png";

class TextCard extends React.Component {
    constructor (props) {
        super (props);
        this.state = {
            marginSize: "20px 20px 20px 20px",
            shadeColour: "rgba(0, 0, 0, 0.5)",
            cardBlur: "blur(0px)",
        }
    }

    render () {
        var themeHighlight = "rgb(255, 230, 109)";
        var cardWrapperStyle = {
            margin: this.state.marginSize,
            transitionDuration: "200ms",
        }
        var cardBackStyle = {
            backgroundColor: "rgba(255, 255, 255, 0.2)",
            backgroundImage: `url(` + this.props.image + `)`,
            backgroundSize: "cover",
            backgroundPosition: "center",
            borderRadius: "6px",
        }
        var titleStyle = {
            color: themeHighlight,
            fontSize: "28px",
            fontFamily: 'Source Sans',
            fontWeight: "600",
            overflow: "hidden",
        }
        var titleWrapperStyle = {
            height: "38px",
            overflow: "hidden",
            paddingBottom: "5px",
            backgroundColor: "rgba(0, 0, 0, 0.5)",
            transition: "all 100ms",
            backdropFilter: this.state.cardBlur,
        }
        var captionStyle = {
            color: "white",
            fontSize: "18px",
            fontFamily: 'Source Sans',
        }
        var iconStyle = {
            color: themeHighlight,
            margin: "auto",
            fontSize: "50px",
        }
        var contentWrapperStyle = {
            overflow: "hidden",
            height: "105px",
            paddingTop: "5px",
            paddingLeft: "10px",
            paddingRight: "10px",
            backgroundColor: "rgba(0, 0, 0, 0.5)",
            transition: "all 100ms",
            backdropFilter: this.state.cardBlur,
        }
        var skillsWrapperStyle = {
            height: "16px",
            paddingBottom: "20px",
            overflow: "hidden",
        }
        var skillsStyle = {
            fontFamily: 'Courier',
            color: themeHighlight,
            fontSize: "16px",
            opacity: "75%",
        }
        var dividerStyle = {
            backgroundColor: themeHighlight,
            opacity: "25%",
            width: "25%",
            marginLeft: "37.5%",
        }
        var shade = {
            backgroundColor: this.state.shadeColour,
            alignItems: "center",
            textAlign: "center",
            padding: "20px",
            paddingBottom: "0",
            borderRadius: "6px",
            transition: "all 200ms",
        }
        return (
            <div style={cardWrapperStyle} onMouseEnter={() => this.setState({ 
                marginSize: "10px 20px 10px 20px",
                shadeColour: "rgba(0, 0, 0, 0.25)",
                cardBlur: "blur(15px)",
                })} onMouseLeave={() => this.setState({
                marginSize: "20px 20px 20px 20px",
                shadeColour: "rgba(0, 0, 0, 0.5)",
                cardBlur: "blur(0px)",
                })}>
                <Link href={this.props.link} color="inherit" underline="none">
                    <Paper style={cardBackStyle} elevation={0}>
                        <div style={shade}>
                            <Stack spacing={1}>
                                <PaletteOutlined style={iconStyle}/>
                                <Paper style={titleWrapperStyle} elevation={5}>
                                    <Typography style={titleStyle}>{this.props.title}</Typography>
                                </Paper>
                                
                                <Paper style={contentWrapperStyle} elevation={0}>
                                    <Typography style={captionStyle}>{this.props.content}</Typography>
                                </Paper>
                                <Divider style={dividerStyle} />
                                <div style={skillsWrapperStyle}>
                                    <Typography style={skillsStyle}> {this.props.skills}</Typography>
                                </div>
                            </Stack>
                        </div>
                    </Paper>
                </Link>
            </div>
        );
    }
}
export default TextCard;