import React, { useEffect } from 'react';

import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";


class LoadBox extends React.Component {

    render () {
        var themeHighlight = "rgb(255, 230, 109)";
        var backgroundCover = {
            backgroundColor: "rgb(32, 32, 32)",
            width: "100%",
            height: "100vh",
            transitionDuration: "200ms",
            overflow: "hidden",
        }
        var titleStyle = {
            color: themeHighlight,
            fontFamily: 'Courier',
            fontSize: "5vmin",
        }
        var divStyle = {
            paddingTop: "45vh",
            paddingLeft: "1vw",
            transitionDuration: "200ms",
        }
        return (
            <div style={backgroundCover} id="mainBox">
                <Stack direction="row" style={divStyle}>
                    <Typography style={{...titleStyle, color: "white", marginRight: "1em", opacity: "50%"}}>[@user ~]$ </Typography>
                    <Typography style={titleStyle} id="loadText"></Typography>
                    <Typography style={{...titleStyle, color: "white", marginRight: "1em", opacity: "50%"}} id="loadText"> |</Typography>
                </Stack>
                
            </div>
        );
    };
    
    componentDidMount () {
        window.scrollTo(0, 0);
        document.documentElement.style.overflow = "hidden";
        let background = document.getElementById("mainBox");
        background.style.height = "100vh";
        let letters = this.props.consoleText;
        document.getElementById("loadText").innerHTML = " ";
        let totalTimeout = 500;
        for (let i = 0; i < letters.length; i++) {
            totalTimeout += (letters.charAt(i) == " " || letters.charAt(i) == "/" || letters.charAt(i) == ".") ? (75) : (50);
            setTimeout(this.addLetters, totalTimeout, letters.charAt(i));
        }
        setTimeout(function () {
            document.getElementById("mainBox").style.height = "0"
            document.getElementById("loadText").style.paddingTop = "0";
            document.documentElement.style.overflow = "auto";
        }, totalTimeout + 150);
    }
    addLetters (letter) {
        window.scrollTo({top: 0, behavior: "smooth"});
        document.getElementById("loadText").innerHTML = document.getElementById("loadText").innerHTML + letter;
    }

    componentWillUnmount () {
        var id = window.setTimeout(function() {}, 0);
        document.documentElement.style.overflow = "auto";
        while (id--) {
            window.clearTimeout(id); // will do nothing if no timeout with id is present
        }
    }

}
export default LoadBox;