import React, { useState } from 'react';

import Typography from "@mui/material/Typography";
import Link from "@mui/material/Link";

import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";

import Divider from "@mui/material/Divider";

import FolderOpenOutlined from "@mui/icons-material/FolderOpenOutlined";

class TextCard extends React.Component {
    
    constructor (props) {
        super (props);
        this.state = {
            marginSize: "20px 20px 20px 20px",
            backColour: "rgba(96, 96, 96, 0.90)"
        }
    }

    render () {
        var themeHighlight = "rgb(255, 230, 109)";
        let cardWrapperStyle = {
            margin: this.state.marginSize,
            transitionDuration: "200ms",
        }
        var cardBackStyle = {
            backgroundColor: this.state.backColour,
            alignItems: "center",
            textAlign: "center",
            padding: "20px",
            paddingBottom: "0",
            borderRadius: "6px",
            transition: "all 200ms",
        }
        var titleStyle = {
            color: themeHighlight,
            fontSize: "28px",
            fontFamily: 'Source Sans',
            fontWeight: "600",
            overflow: "hidden",
        }
        var titleWrapperStyle = {
            height: "38px",
            overflow: "hidden",
            paddingBottom: "5px",
        }
        var captionStyle = {
            color: "white",
            fontSize: "18px",
            fontFamily: 'Source Sans',
        }
        var iconStyle = {
            color: themeHighlight,
            margin: "auto",
            fontSize: "50px",
            height: "50px",
        }
        var contentWrapperStyle = {
            overflow: "hidden",
            height: "105px",
            paddingTop: "5px",
        }
        var skillsWrapperStyle = {
            height: "16px",
            paddingBottom: "20px",
            overflow: "hidden",
        }
        var skillsStyle = {
            fontFamily: 'Courier',
            color: themeHighlight,
            fontSize: "16px",
            opacity: "75%",
        }
        var dividerStyle = {
            backgroundColor: themeHighlight,
            opacity: "25%",
            width: "25%",
            marginLeft: "37.5%",
        }
        return (
            
            
            <div style={cardWrapperStyle} onMouseEnter={() => this.setState({ 
                marginSize: "10px 20px 10px 20px",
                backColour: "rgba(96, 96, 96, 1.0)"
                })} onMouseLeave={() => this.setState({
                marginSize: "20px 20px 20px 20px",
                backColour: "rgba(96, 96, 96, 0.90)"
                })}>
                <Link href={this.props.link} color="inherit" underline="none">
                    <Paper style={cardBackStyle} elevation={0}>
                        <Stack spacing={1}>
                            <FolderOpenOutlined style={iconStyle}/>
                            <div style={titleWrapperStyle}>
                                <Typography style={titleStyle}>{this.props.title}</Typography>
                            </div>
                            
                            <div style={contentWrapperStyle}>
                                <Typography style={captionStyle}>{this.props.content}</Typography>
                            </div>
                            <Divider style={dividerStyle} />
                            <div style={skillsWrapperStyle}>
                                <Typography style={skillsStyle}> {this.props.skills}</Typography>
                            </div>
                        </Stack>
                    </Paper>
                </Link>
            </div>
                
            
            
        );

    }
}
export default TextCard;