import React from 'react';

import Typography from "@mui/material/Typography";

import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";

import Button from "@mui/material/Button";
import ButtonGroup from "@mui/material/ButtonGroup";


import {BrowserRouter, Routes, Route, NavLink} from "react-router-dom"

import Home from "./Home";
import Projects from "./Projects";
import Contact from "./Contact";
import Roadmap from "./Roadmap";

class Base extends React.Component {
    //isActive => ({color: isActive ? navBarTextColor : navBarTextColorInactive
    render () {
        
        var navBarColor = "rgba(0, 0, 0, 0.5)";
        var navBarStyle = {
            backgroundColor: navBarColor,
            backdropFilter: 'blur(15px)',
            width: "100%",
            overflow: "hidden",
            borderRadius: '0',
            position: "fixed",
            top: "0",
            zIndex: "998",
            transition: "all 300ms",
            height: "65px",
            borderBottom: "1px solid rgba(255, 255, 255, 0.2)",
        }
    
        var navBarTextColor = "rgb(255, 255, 255)";
        var siteTitleStyle = {
            color: navBarTextColor,
            fontSize: "35px",
            fontFamily: 'Source Sans',
            paddingRight: "20px",
            paddingTop: "7px",
            marginLeft: "20px",
        }
        var titleWrapperStyle = {
            transitionDuration: "300ms",
            width: "25vw",
            flexDirection: "row",
            overflow: "hidden",
            whiteSpace: "nowrap",
        }
        var navButtonStyle = {
            transition: "all 300ms",
            color: navBarTextColor,
            borderColor: 'rgba(255, 255, 255, 0.35)',
            fontSize: "20px",
            padding: "0 20px 0 20px",
            height: "100%",
        }
        var navButtonText = {
            color: "rgb(255, 230, 109)",
            textDecoration: "none"
        }
        var buttonWrapperStyle = {
            width: "50vw",
            height: "100%",
            paddingTop: "15px",
            padding: "auto",
            display: "flex",
            justifyContent: "center",
            transitionDuration: "300ms",
            marginLeft: "0",
            marginRight: "0",
        };
        var menuWrapper = {
            width: "25vw",
            height: "10px",
            marginLeft: "0",
            transitionDuration: "300ms",
        }
        return (
            <div>
                <BrowserRouter>
                    <Paper style={navBarStyle} id="navBar" elevation={3}>
                        <Stack spacing={2} direction="row">
                            <div id="titleWrapper" style={titleWrapperStyle}> <Typography style={siteTitleStyle} id="title">Partialhedgehog.com</Typography> </div>
                            <div style={buttonWrapperStyle} id="buttonWrapper">
                                <ButtonGroup variant="text">
                                    <Button style={navButtonStyle} name="navButton"><NavLink to="/" style={navButtonText}>Home</NavLink></Button>
                                    <Button style={navButtonStyle} name="navButton"><NavLink to="/projects" style={navButtonText}>Projects</NavLink></Button>
                                    <Button style={navButtonStyle} name="navButton"><NavLink to="/contact" style={navButtonText}>Contact</NavLink></Button>
                                </ButtonGroup>
                            </div>
                            <div style={menuWrapper} id="menuWrapper"/>
                        </Stack>    
                    </Paper>        
                    <Routes>
                        <Route path="/" element={<Home />}/>
                        <Route path="/projects" element={<Projects />}/>
                        <Route path="/contact" element={<Contact />}/>
                    </Routes>
                </BrowserRouter>
            </div>
        );
    }
    componentDidMount () {
        window.addEventListener('scroll', this.handleScroll);
        window.addEventListener('resize', this.handleScroll);
        if (document.documentElement.clientWidth < 768) {
            document.getElementById("titleWrapper").style.width = "0vw";
            document.getElementById("menuWrapper").style.width = "0vw";
            document.getElementById("buttonWrapper").style.width = "100%";
        }
    }
    componentWillUnmount () {
        window.removeEventListener('scroll', this.handleScroll);
    }
    handleScroll (e) {
        let navbar = document.getElementById("navBar");
        let titleWrapper = document.getElementById("titleWrapper");
        let buttonWrapper = document.getElementById("buttonWrapper");
        let menuWrapper = document.getElementById("menuWrapper");
        var buttons = document.getElementsByName("navButton");
        if (document.documentElement.scrollTop < document.documentElement.clientHeight/5) {
            this.navBarTextColor = "rgb(255, 255, 255)";
            navbar.style.backgroundColor = "rgba(0, 0, 0, 0.50)";
            navbar.style.height = "65px";
            buttonWrapper.style.paddingTop = "15px";
            if (document.documentElement.clientWidth < 860) {
                titleWrapper.style.width = "0vw";
                menuWrapper.style.width = "0vw";
                buttonWrapper.style.width = "100%";
            } else {
                titleWrapper.style.width = "25vw";
                menuWrapper.style.width = "25vw";
                buttonWrapper.style.width = "50vw";
            }
            
            
            for (var i = 0; i < buttons.length; i++) {
                buttons[i].style.padding = "0 20px 0 20px";
            }
        } else {
            this.navBarTextColor = "rgb(0, 0, 0)";
            navbar.style.height = "45px";
            navbar.style.backgroundColor = "rgba(96, 86, 96, 0.50)";
            buttonWrapper.style.paddingTop = "5px";
            titleWrapper.style.width = "0vw";
            menuWrapper.style.width = "0vw";
            buttonWrapper.style.width = "100vw";
            for (var i = 0; i < buttons.length; i++) {
                buttons[i].style.padding = "0 40px 0 40px";
            }
        }
        // handle background scroll
        document.body.style.backgroundPositionY = document.documentElement.scrollTop/-5 + "px";
    }
}
  
export default Base;
  