import React, { useEffect } from 'react';

import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Link from "@mui/material/Link";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";

import ArrowForwardRoundedIcon from '@mui/icons-material/ArrowForwardRounded';

import {createTheme, ThemeProvider} from "@mui/material/styles";
import TextCard from "./TextCard";
import ImageCard from "./ImageCard";

import LoadBox from "./LoadBox";

import manta from "./img/manta.jpg";
import architecture from "./img/architecture.jpg";
import mountains from "./img/mountains.jpg";
import ship from "./img/ship.jpg";
import landing from "./img/landing.jpg";
import freighter from "./img/freighter.jpg";
import arctic from "./img/arctic.jpg";

const theme = createTheme({
    breakpoints: {
      values: {
            xs: 0,
            sm: 600,
            md: 1024,
            lg: 1600,
            xl: 2150,
            xxl: 4000,
        },
    },
});

class Projects extends React.Component {
    
    render () {
        let miniDist = 12;
        let smallDist = 6;
        let midDist = 4;
        let largeDist = 3;
        let hugeDist = 2;
        let massiveDist= 1;

        let themeHighlight = "rgb(255, 230, 109)";

        let mainGridStyle = {
            paddingTop: "100px",
        }
        
        let nextButton = {
            color: "black",
            backgroundColor: themeHighlight,
            fontFamily: "Source Sans",
            fontSize: "20px",
            borderRadius: "15px",
            padding: "10px",
            margin: "auto",
            fontWeight: "600",
        }

        let contentPageStyle = {
            backgroundColor: "rgb(0, 0, 0)",
            padding: "100px",
            justifyContent: "center",
            display: "flex",
        }
        let arrowStyle = {
            color: "black",
            fontSize: "30px"
        }
        return (
            <ThemeProvider theme={theme}>
            <div>
                <LoadBox consoleText="ls ~/projects"/>
                <Grid container style = {mainGridStyle} justifyContent = "center">
                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <TextCard title="Image  Processor" content="
                        Reading and processing images bit-by-bit to create neural network training datasets.
                        " link="https://github.com/ryanchan8662/char-network" skills="C ~ Assembly" />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <TextCard title="Log Parser" content="
                        A custom log parser to locate, filter out, automatically block malicious server requests and IPs.
                        " link="https://github.com/ryanchan8662/apachelogparser" skills="Java ~ RESTful ~ Apache 2" />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <TextCard title="Personal Site" content="
                        Built to showcase my work in an easily accessible online package for anyone to view, anytime. Upgraded overtime, switching to new technologies.
                        " link="/" skills="React ~ HTML ~ CSS ~ Apache 2" />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <TextCard title="Service Prototype" content="
                        A first-place interactive prototype for a web service concept. Designed from the ground up to be accessible
                        to users of all ages.
                        " link="/" skills="React" />
                    </Grid>
                    
                    
                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <ImageCard title="Manta Aircraft" content="
                        Combination of hardsurface modelling and procedural/geometry-aware texturing. Exploring creative twists to animals.
                        " link="/files/manta.blend" skills="Blender ~ Textures ~ Topology" image={manta} />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <TextCard title="Software Engineering" content="
                        Drafted a software requirements specification with the most commonly-used diagrams to design a cryptocurrency API client.
                        " skills="UML Diagrams ~ Java" />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <TextCard title="Product Sprint" content="
                        Concept for cybersecurity improvements in modern dating apps to prevent romance scams.
                        " skills="Affinity Designer ~ Figma" />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <TextCard title="Regression Model" content="
                        Dataset processing and Tensorflow random forest model training for historical housing prices. Prediction accurate to within ~$20k.
                        " skills="Tensorflow ~ R ~ Python" />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <TextCard title="Previous Site" content="
                        The previous version of this site, written without the help of libraries or templates. Allowed for the progression onto React.
                        " skills="HTML ~ CSS ~ Javascript" />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <ImageCard title="Architecture" content="
                        A heavy focus on Gothic Revival architectural design and snowy, mountainous landscapes. A continuation of a previous landscape project.
                        " skills="Mesh Assets ~ Shaders" image={architecture} />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <ImageCard title="Mountains" content="
                        Exploration into complex mountainous terrain, foliage, and procedural geometrically-aware shaders.
                        " skills="Blender ~ Shaders" image={mountains} />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <TextCard title="Maze Pathfinder" content="
                        A node-based algorithm implementation to intake images and find a solution path, plotting it as a line. 30 solutions per second.
                        " link="https://github.com/ryanchan8662/pathfinder" skills="Java ~ Graphs ~ Optimisation" />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <ImageCard title="Ship in a Bottle" content="
                        The Canadian Coast Guard Ship (CCGS) Amundsen, in a miniature scene. Made in under a week from scratch.
                        " skills="Blender ~ Modelling" image={ship} />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <TextCard title="String Filter" content="
                        A program in ARM 7 assembly language which removes all instances of a particular isolated string, with a strict restriction on instruction length.
                        " link="/files/stringfilter.s" skills="Assembly ~ ARM 7" />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <ImageCard title="Rainy Landing" content="
                        A first-place study in volumetrics, hardsurface modelling, rain effects, and postprocessing.
                        " skills="Volumetrics ~ Hardsurface" image={landing} />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <ImageCard title="Interstellar Freighter" content="
                        Heavy application of Quixel Mixer for mesh-dependent textures to create effects like rust, scratches, and grime.
                        " skills="Quixel Mixer ~ Detailing" image={freighter} />
                    </Grid>

                    <Grid item xs={miniDist} sm={smallDist} md={midDist} lg={largeDist} xl={hugeDist} xxl={massiveDist}>
                        <ImageCard title="Arctic Flight" content="
                        Exploring the limits of a simple procedurally generated mesn and textures. Paired with a detailed prop plane.
                        " skills="Landscape ~ Procedural generation" image={arctic} />
                    </Grid>
                </Grid>
                {/*<Paper style={{...contentPageStyle, backgroundColor: "rgb(16, 16, 21)"}}>
                    <Stack spacing={2} style={{textAlign: "center", border: "2px solid rgb(255, 230, 109)", padding: "30px", borderRadius: "12px"}}>
                        <Typography style={{color: "white", fontFamily: "Source Sans", fontSize: "30px"}}>Like what you see?</Typography>
                        <Link href="/contact" style={{textDecoration: "none"}}>
                            <Button style={nextButton}>Contact me <ArrowForwardRoundedIcon style={arrowStyle}/></Button>
                        </Link>
                    </Stack>
                </Paper>*/}
            </div>
            </ThemeProvider>
            
        );
    };
}
export default Projects;